  padding-top: 1rem;
}
.sub-footer ul {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: center;
}
@media (min-width: 769px) {
  .sub-footer ul {
    justify-content: flex-end;
  }
}
.sub-footer ul li a {
  color: #595e67;
}